import { Component, OnInit, Renderer2, ElementRef, ViewChild, AfterViewInit, trigger, state, style, transition, animate } from '@angular/core';
import * as echarts from 'echarts';
import { ResumeService } from './resume.service';
declare var THREE: any;
@Component({
    selector: 'app-resume',
    templateUrl: './resume.component.html',
    styleUrls: ['./resume.component.scss'],
    providers: [ResumeService],
    animations: [
        trigger('CardAnimation', [
            state('up', style({
                position: 'absolute',
                top: '-100%'
            })),
            state('down', style({
                position: 'absolute',
                top: '0'
            })),
            transition('up <=> down', animate('1000ms cubic-bezier(0.645, 0.045, 0.355, 1)'))
        ])
    ]
})
export class ResumeComponent implements OnInit, AfterViewInit {
    scene: any;
    camera: any;
    stats: any;
    renderer: any;
    plane: any;
    controls: any;
    index: any = 1;

    render: any;
    @ViewChild('techChart') techChart: ElementRef;
    @ViewChild('dataView') dataView: ElementRef;
    @ViewChild('uiView') uiView: ElementRef;
    constructor(private _el: ElementRef, private _render: Renderer2, public _resume: ResumeService) { }

    ngOnInit() {
        if (this.IsPC()) {
            document.oncontextmenu = function () {
                return false;
            };
            document.addEventListener('mousedown', (event: any) => {
                if (event.button === 0) {
                    if (this.index < 5) {
                        this.index += 1;
                    } else {
                        this.index = 1;
                    }
                    if (this.index === 2) {
                        this.techTreeRender();
                    }
                    if (this.index === 4) {
                        this.projectRender();
                    }
                    if (this.index === 5) {
                        document.getElementById('banana').removeChild(this.renderer.domElement);
                    }
                } else if (event.button === 2) {
                    if (this.index > 1) {
                        this.index -= 1;
                    } else {
                        this.index = 5;
                    }
                    if (this.index === 2) {
                        this.techTreeRender();
                    }
                    if (this.index === 4) {
                        this.projectRender();
                    }
                    if (this.index === 3) {
                        document.getElementById('banana').removeChild(this.renderer.domElement);
                    }
                    return false;
                }
            });
        }
        if (!this.IsPC()) {
            document.addEventListener('touchend', () => {
                if (this.index < 7) {
                    this.index += 1;
                } else {
                    this.index = 1;
                }
                if (this.index === 2) {
                    this.techTreeRender();
                }
                if (this.index === 4) {
                    this.initChartData();
                }
                if (this.index === 5) {
                    this.initChartUI();
                }
                if (this.index === 6) {
                    this.initBanana();
                }
                if (this.index === 7) {
                    document.getElementById('banana').removeChild(this.renderer.domElement);
                }
            });
        }

    }
    ngAfterViewInit() {

    }
    IsPC() {
        const userAgentInfo = navigator.userAgent;
        const Agents = ['Android', 'iPhone',
            'SymbianOS', 'Windows Phone',
            'iPad', 'iPod'];
        let flag = true;
        for (let v = 0; v < Agents.length; v++) {
            if (userAgentInfo.indexOf(Agents[v]) > 0) {
                flag = false;
                break;
            }
        }
        return flag;
    }

    initChartData() {
        const chartDataView = echarts.init(this.dataView.nativeElement, 'dark');
        const dataViewData = this._resume.dataView;
        chartDataView.setOption(dataViewData);
    }

    initChartUI() {
        const chartUiView = echarts.init(this.uiView.nativeElement, 'dark');
        const uiViewData = this._resume.uiView;
        chartUiView.setOption(uiViewData);

    }

    dataViewOpen() {
        window.open('https://skadieyes.github.io/chartset-build');
        this.index = 4;
        this.initBanana();
    }

    dataCodeOpen() {
        window.open('https://github.com/skadieyes/chart-set');
        this.index = 4;
        this.initBanana();
    }
    bananaViewOpen() {
        window.open('https://skadieyes.github.io/three-js/app/flyGame/index.html');
        this.index = 4;
        this.initBanana();
    }
    bananaCodeOpen() {
        window.open('https://github.com/skadieyes/three-js');
        this.index = 4;
        this.initBanana();
    }

    initBanana() {
        const texture = new THREE.Texture();
        const loader2 = new THREE.ImageLoader();
        loader2.load('assets/textures/Banana.png', function (image: any) {
            texture.image = image;
            texture.needsUpdate = true;
        });

        const loader = new THREE.OBJLoader();
        loader.load('assets/obj/Banana.obj', (loadedMesh: any) => {
            const material = new THREE.MeshLambertMaterial({
                color: 0xccc
            });
            loadedMesh.children.forEach((child: any) => {
                child.material.map = texture;
                child.geometry.computeFaceNormals();
                child.geometry.computeVertexNormals();
            });
            loadedMesh.scale.set(150, 150, 150);
            loadedMesh.rotation.x = -0.3;
            this.scene.add(loadedMesh);
        });

        this.sceneSet();
        this.cameraSet();
        this.rendererSet();
        this.lightSet();
        this.renderRun();
        document.getElementById('banana').appendChild(this.renderer.domElement);
    }
    projectRender() {
        this.initChartData();
        this.initChartUI();
        this.initBanana();
    }

    techTreeRender() {
        const chart = echarts.init(this.techChart.nativeElement, 'dark');
        const techData = this._resume.techTree;
        chart.setOption(techData);
    }
    bodyInit() {
        const el = document.getElementsByClassName('resume-body');
        if (el.length > 0) {
            for (let i = 0; i < el.length; i++) {
                this._render.setStyle(el[i], 'height', window.innerHeight + 'px');
            }
        }
    }
    sceneSet() {
        this.scene = new THREE.Scene();
        // this.scene.fog = new THREE.Fog(0xffffff, 0.015, 100);
    }
    cameraSet() {
        this.camera = new THREE.PerspectiveCamera(45, 1, 0.1, 1000);
        this.camera.position.x = -30;
        this.camera.position.y = 40;
        this.camera.position.z = 30;
        this.camera.lookAt(this.scene.position);
    }

    rendererSet() {
        this.renderer = new THREE.WebGLRenderer();
        this.renderer.setClearColor(new THREE.Color('#333333'));
        this.renderer.setSize(200, 200);
        this.renderer.shadowMapEnabled = true;
    }
    lightSet() {

        const ambientLight = new THREE.AmbientLight(0x0c0c0c);
        this.scene.add(ambientLight);

        // add spotlight for the shadows
        const spotLight = new THREE.SpotLight(0xffffff);
        spotLight.position.set(-40, 60, 20);
        spotLight.castShadow = true;
        this.scene.add(spotLight);
    }
    renderRun(): void {
        this.renderer.render(this.scene, this.camera);
        this.render = () => {
            //    this.stats.update();
            window.requestAnimationFrame(this.render);
            this.scene.traverse((e: any) => {
                if (e instanceof THREE.Mesh && e !== this.plane) {

                    e.rotation.x += 0.03;
                    e.rotation.y += 0.03;
                    e.rotation.z += 0.03;
                }
            });
            this.renderer.render(this.scene, this.camera);
        };
        window.requestAnimationFrame(this.render);
    }

}



